import request from "./http";

// 用户主页
export function getUserPage(uid){
    return request({
        url:"user/" + uid,
        method:"get"
    })
}

// 用户专辑
export function getUserCollection(uid){
    return request({
        url:"user/"+uid+"/collections",
        method:"get",
    })
}

/**
 * 用户作品
 * @param {String} order 类型 (默认空 view最热 new最新)
 * @param {String} page 分页页码
 */
export function getUserArticles(uid,params){
    return request({
        url:"user/"+ uid +"/articles",
        method:"get",
        params
    })
}

/**
 * 用户喜欢
 * @param {string} lastIndexId 分页起始id 上一页最后一项id
 */
export function getUserLike(uid,params){
    return request({
        url:"user/"+uid+"/articles-approved",
        method:"get",
        params
    })
}

/**
 * 用户粉丝
 * @param {number} page 分页页码
 */
export function getUserFollowers(uid,params){
    return request({
        url:"user/"+uid+"/followers",
        method:"get",
        params
    })
}

/**
 * 用户关注
 * @param {number} page 分页页码
 */
export function getUserFollowees(uid,params){
    return request({
        url:"user/"+uid+"/followees",
        method:"get",
        params
    })
}