<template>
  <div>
    <div class="searchMsg" v-if="userInfo">
      <div
        class="top"
        :style="{
          'background-image': 'url(' + userInfo.app_background!='' ? userInfo.app_background : '../assets/img/Db.png' + ')',
        }"
      >
        <div class="mask">
          <van-sticky @scroll="stickyBgChange">
            <div class="icons" ref="topNav">
              <span class="iconfont icon-xiangzuojiantou" @click="goPrev"></span>
              <p class="nickname" v-show="scrollChange">
                {{ userInfo.username }}
              </p>
              <span class="iconfont icon-gengduo1" @click="showShare = true"></span>
            </div>
          </van-sticky>
          <div class="avatar">
            <img :src="userInfo.avatar" alt="" />
          </div>
          <h2>{{ userInfo.username }}</h2>
          <p class="p1">
            {{ userInfo.occupation}} {{ userInfo.ip_location }}
          </p>
          <p class="p2">{{ userInfo.verify_description }}</p>
          <div class="topEnd">
            <div class="count">
              <div class="popularity">
                <p class="countP1">
                  {{ userInfo.count.count_popularity | CountHandle }}
                </p>
                <p class="countP2">人气</p>
              </div>
              <router-link tag="div" :to="'/followview?id=' + userInfo.id + '&type=followers'" class="fans">
                <p class="countP1">
                  {{ userInfo.count.count_follower | CountHandle }}
                </p>
                <p class="countP2">粉丝</p>
              </router-link>
              <router-link tag="div" :to="'/followview?id=' + userInfo.id + '&type=followees'" class="follow">
                <p class="countP1">
                  {{ userInfo.count.count_followee | CountHandle }}
                </p>
                <p class="countP2">关注</p>
              </router-link>
            </div>
            <div class="btn">关注</div>
          </div>
        </div>
      </div>
      <div class="nav">
        <div :class="{ active: navIndex == 0 }" @click="navIndex = 0">作品</div>
        <div :class="{ active: navIndex == 1 }" @click="navIndex = 1">专辑</div>
        <div :class="{ active: navIndex == 2 }" @click="getUserLikeFun">
          喜欢
        </div>
        <div :class="{ active: navIndex == 3 }" @click="navIndex = 3">资料</div>
      </div>
      <!-- 作品 -->
      <div class="works" v-show="navIndex == 0">
        <div class="paixu" @click="showMenu">
          {{ articlesOrder[orderIndex] }}
          <span class="iconfont icon-xiangxiazhankai"></span>
          <div class="dropdownMenu" v-show="menuShow">
            <p
              v-for="(item, index) in articlesOrder"
              :key="index"
              :class="{ active: index == orderIndex }"
              @click="orderChange(index)"
            >
              {{ item }}
            </p>
          </div>
        </div>
        <ul class="worksInfo" v-if="articlesList">
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
            <router-link
              tag="li"
              :to="'/videoplay?id=' + item.resource.id"
              v-for="item in articlesList"
              :key="item.resource.id"
            >
              <div class="img">
                <img :src="item.resource.cover" alt="" />
                <div class="time">
                  {{ item.resource.duration | playerTime }}
                </div>
              </div>
              <div class="text">
                <h3>{{ item.resource.title }}</h3>
                <p class="times">
                  {{ (item.resource.publish_time * 1000) | changeTimeFormat }}
                </p>
                <div class="textBottom">
                  <p>
                    {{ item.resource.count.count_like | CountHandle }} 喜欢 ·
                    {{ item.resource.count.count_view | CountHandle }} 播放
                  </p>
                  <span class="iconfont icon-gengduo1" @click.prevent="showShare = true"></span>
                </div>
              </div>
            </router-link>
          </van-list>
        </ul>
      </div>
      <!-- 专辑 -->
      <div class="album" v-show="navIndex == 1">
        <div class="albumInfo">专辑功能暂未开放~</div>
      </div>
      <!-- 喜欢 -->
      <div class="like" v-show="navIndex == 2" v-if="likeList">
        <div class="topMargin"></div>
        <div v-if="likeList.length != 0">
          <router-link tag="div" :to="'videoplay?id='+item.resource.id" class="likeInfo" v-for="item in likeList" :key="item.resource.id">
          <div class="top">
            <div class="userInfo">
              <div class="img">
                <img :src="item.resource.author.userinfo.avatar" alt="" />
              </div>
              <p>
                {{ item.resource.author.userinfo.username }}
              </p>
            </div>
            <span class="iconfont icon-gengduo1" @click.prevent="showShare = true"></span>
          </div>
          <p class="text">{{ item.resource.title }}</p>
          <div class="img" v-if="videoLoading">
            <img :src="item.resource.cover" alt="" />
            <div class="imgBottom">
              <p>
                {{ item.resource.count.count_view | CountHandle }}
                观看 |
                {{ item.resource.duration | playerTime }}
              </p>
            </div>
          </div>
          <div class="video" v-else>
            <video
              src="../video/fun.mp4"
              ref="video"
              @canplay="oncanplayFun"
              @timeupdate="ontimeupdateFun"
              muted
            ></video>
            <div class="process">
              <div class="line" :style="{ width: lineWidth }"></div>
            </div>
            <div class="iconfont icon-shengyinguan"></div>
            <div class="iconfont icon-shengyinkai icondisplay"></div>
          </div>
          <div class="bottom">
            <div class="shoucang">
              <span class="iconfont icon-shoucang"></span>
              {{ item.resource.count.count_collect }}
            </div>
            <div class="pinglun">
              <span class="iconfont icon-pinglun1"></span>
              {{ item.resource.count.count_comment }}
            </div>
            <div class="dianzan">
              <span class="iconfont icon-dianzan1"></span>
              {{ item.resource.count.count_like }}
            </div>
          </div>
        </router-link>
        </div>
        <div class="likeTips" v-else>用户还没有喜欢的作品~</div>
      </div>
      <!-- 资料 -->
      <div class="info" v-show="navIndex == 3">
        <div class="basicInfo">
          <h3>基本资料</h3>
          <div class="text">
            <div>
              <p class="left">性别</p>
              <p class="right">{{ userInfo.sex == 1 ? "男" : "女" }}</p>
            </div>
            <div>
              <p class="left">年龄</p>
              <p class="right">{{ userInfo.birthday | getAge }}</p>
            </div>
            <div v-if="userInfo.location">
              <p class="left">所在地区</p>
              <p
                class="right"
                v-if="
                  userInfo.location.province ||
                  userInfo.location.country ||
                  userInfo.location.city
                "
              >
                <span v-if="userInfo.location.province"
                  >{{ userInfo.location.province.name }}
                </span>
                <span v-if="userInfo.location.country"
                  >{{ userInfo.location.country.name }}
                </span>
                <span v-if="userInfo.location.city">{{
                  userInfo.location.city.name
                }}</span>
              </p>
              <p class="right" v-else>未知</p>
            </div>
            <div>
              <p class="left">简介</p>
              <p class="right">
                {{ userInfo.about }}
              </p>
            </div>
          </div>
        </div>
        <div class="basicInfo">
          <h3>教育背景</h3>
          <div class="text">
            <div>
              <p class="left">学校</p>
              <p class="right">{{ userInfo.graduation }}</p>
            </div>
          </div>
        </div>
        <div
          class="basicInfo"
          v-if="
            userInfo.occupation ||
            userInfo.entry_year ||
            userInfo.verify_description
          "
        >
          <h3>认证信息</h3>
          <div class="text">
            <div v-if="userInfo.occupation">
              <p class="left">认证身份</p>
              <p class="right">{{ userInfo.occupation }}</p>
            </div>
            <div v-if="userInfo.entry_year">
              <p class="left">入行年份</p>
              <p class="right">{{ userInfo.entry_year }}</p>
            </div>
            <div v-if="userInfo.verify_description">
              <p class="left">认证描述</p>
              <p class="right">{{ userInfo.verify_description }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="loadingView" v-else>
      <div class="loading"></div>
      <van-loading type="spinner" size="20" vertical />
    </div>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
    />
  </div>
</template>

<script>
import { getUserPage, getUserArticles, getUserLike } from "../api/user";
export default {
  data() {
    return {
      scrollChange: false,
      userInfo: null,
      menuShow: false,
      navIndex: 0,
      articlesPage: 1,
      articlesOrder: ["默认排序", "热门排序", "最新上传"],
      orderIndex: 0,
      articlesList: [],
      loading: false,
      finished: false,
      // 喜欢
      current: 0,
      active: 0,
      durationTime: 1,
      currentTime: 0,
      lineWidth: "0",
      videoLoading: true,
      likeList: null,
      showShare: false,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    oncanplayFun() {
      this.durationTime = this.$refs.video.duration;
    },
    ontimeupdateFun() {
      this.currentTime = this.$refs.video.currentTime;
      this.lineWidth = (this.currentTime / this.durationTime) * 100 + "%";
    },

    getUserLikeFun() {
      if (this.navIndex == 2) {
        return;
      }
      this.navIndex = 2;
      getUserLike(this.$route.query.id).then((data) => {
        // console.log(data);
        this.likeList = data.data.list;
      });
    },
    getUserPageFun() {
      let uid = this.$route.query.id;
      getUserPage(uid).then((data) => {
        // console.log(data);
        this.userInfo = data.data;
      });
    },
    onLoad() {
      let uid = this.$route.query.id;
      let order = "";
      if (this.orderIndex == 1) {
        order = "view";
      } else if (this.orderIndex == 2) {
        order = "new";
      }
      getUserArticles(uid, { page: this.articlesPage, order: order }).then(
        (data) => {
          // console.log(data);
          let newData = data.data.list;

          if (this.articlesPage > 1) {
            for (let i = 0; i < newData.length; i++) {
              this.articlesList.push(newData[i]);
            }
          } else {
            // 第一页
            this.articlesList = newData;
          }

          // 加载状态结束
          this.loading = false;

          // 数据全部加载完成
          if (this.articlesList.length >= data.data.total) {
            this.finished = true;
          }
          this.articlesPage += 1;
        }
      );
    },
    orderChange(index) {
      if (index == this.orderIndex) {
        return;
      }
      this.orderIndex = index;
      this.articlesList = [];
      this.articlesPage = 1;
      this.onLoad();
    },
    stickyBgChange(data) {
      if (data.scrollTop > 82) {
        this.$refs.topNav.style.background = "#fff";
        this.$refs.topNav.style.color = "#000";
        this.scrollChange = true;
      } else {
        this.$refs.topNav.style.background = "transparent";
        this.$refs.topNav.style.color = "#fff";
        this.scrollChange = false;
      }
    },
    stopPrev(e) {
      e.preventDefault();
      e.stopPropagation();
    },
    showMenu() {
      // console.log(111);
      if (this.menuShow) {
        this.menuShow = false;
        document.body.removeEventListener("touchmove", this.stopPrev, {
          passive: false,
        });
        document.body.removeEventListener("mousewheel", this.stopPrev, {
          passive: false,
        });
      } else {
        this.menuShow = true;
        document.body.addEventListener("touchmove", this.stopPrev, {
          passive: false,
        });
        document.body.addEventListener("mousewheel", this.stopPrev, {
          passive: false,
        });
      }
    },
    goPrev(){
      this.$router.go(-1);
    }
  },
  created() {
    this.getUserPageFun();
    // this.getUserArticlesFun();
  },
  mounted() {},
  filters: {
    getAge(val) {
      if (val == "") {
        return "未知";
      }
      let ageYear = val.slice(0, 4);
      let nowYear = new Date().getFullYear();
      return nowYear - ageYear;
    },
    CountHandle(val) {
      if (val < 10000) {
        return val;
      }
      let w = val > 10000 ? val / 10000 : val; //万计算
      return w.toFixed(1) + "w";
    },
    playerTime(data) {
      let m = parseInt(data / 60);
      let s = parseInt(data % 60);
      if (m == 0 && s == 0) {
        return "00'00''";
      } else {
        return (m < 10 ? "0" + m : m) + "'" + (s < 10 ? "0" + s : s) + "''";
      }
    },
    changeTimeFormat(timespan) {
      var dateTime = new Date(timespan); // 将传进来的字符串或者毫秒转为标准时间
      var year = dateTime.getFullYear();
      var month = dateTime.getMonth() + 1;
      var day = dateTime.getDate();
      // var hour = dateTime.getHours();
      // var minute = dateTime.getMinutes();
      // var second = dateTime.getSeconds()
      var millisecond = dateTime.getTime(); // 将当前编辑的时间转换为毫秒
      var now = new Date(); // 获取本机当前的时间
      var nowNew = now.getTime(); // 将本机的时间转换为毫秒
      var milliseconds = 0;
      var timeSpanStr;
      milliseconds = nowNew - millisecond;
      if (
        milliseconds > 1000 * 60 * 60 * 24 * 15 &&
        year === now.getFullYear()
      ) {
        timeSpanStr = month + "-" + day;
      } else {
        timeSpanStr = year + "-" + month + "-" + day;
      }
      return timeSpanStr;
    },
  },
};
</script>

<style lang="less">
.searchMsg {
  width: 100%;
  .top {
    width: 100%;
    height: 302px;
    background: url("../assets/img/Db.png") center;
    background-size: cover;
    .mask {
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.2);
    }
    .icons {
      width: 100%;
      height: 42px;
      color: #fff;
      display: flex;
      justify-content: space-between;
      align-items: center;
      p.nickname {
        font-size: 17px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: hidden;
      }
      .iconfont {
        font-size: 15px;
        font-weight: bold;
      }
      .icon-xiangzuojiantou {
        padding-left: 20px;
      }
      .icon-gengduo1 {
        padding-right: 20px;
      }
    }
    .avatar {
      width: 55px;
      height: 55px;
      border-radius: 50%;
      overflow: hidden;
      margin-top: 20px;
      margin-left: 20px;
      margin-bottom: 13px;
      img {
        width: 55px;
        height: 55px;
      }
    }
    h2 {
      font-size: 18px;
      height: 38px;
      line-height: 38px;
      color: #fff;
      overflow: hidden;
      margin: 0 20px;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .p1,
    .p2 {
      font-size: 12px;
      line-height: 24px;
      height: 24px;
      color: rgba(255, 255, 255, 0.7);
      margin-left: 20px;
      white-space: nowrap; /*默认normal 自动换行*/
      overflow: hidden;
      text-overflow: ellipsis; /*默认是clip 超出部分直接切除*/
    }
    .topEnd {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-left: 20px;
      margin-right: 20px;
      margin-top: 10px;
      .count {
        display: flex;
        div {
          flex: 1;
          text-align: center;
          margin-right: 8px;
          .countP1 {
            font-size: 12px;
            line-height: 24px;
            color: #fff;
            font-weight: bold;
          }
          .countP2 {
            font-size: 12px;
            color: rgba(255, 255, 255, 0.7);
          }
        }
      }
      .btn {
        width: 68px;
        height: 26px;
        background-color: #d55644;
        color: #fff;
        font-size: 12px;
        line-height: 26px;
        text-align: center;
        border-radius: 13px;
      }
    }
  }
  .nav {
    width: 100%;
    height: 44px;
    display: flex;
    border-bottom: 1px solid #f1f1f1;
    div {
      flex: 1;
      line-height: 44px;
      text-align: center;
      font-size: 14px;
    }
    .active {
      color: #cf412d;
    }
  }
  .works {
    background-color: #f7f7f7;
    .paixu {
      //   width: 100%;
      height: 46px;
      font-size: 12px;
      color: #5b5b5b;
      background-color: #fff;
      padding-left: 20px;
      position: relative;
      .iconfont {
        font-size: 12px;
        line-height: 46px;
      }
      .dropdownMenu {
        position: absolute;
        background-color: #fff;
        height: 105px;
        line-height: 35px;
        font-size: 12px;
        left: 20px;
        bottom: -105px;
        z-index: 100;
        box-shadow: 0px 0px 11px 0px #e5e5e6;
        p {
          padding: 0 15px;
          border-bottom: 1px solid #f7f7f7;
          &.active {
            color: #cf412d;
          }
        }
      }
    }
    .worksInfo {
      margin-top: 8px;
      background-color: #fff;
      padding-top: 15px;
      li {
        display: flex;
        margin-left: 20px;
        padding-bottom: 10px;
        .img {
          width: 154px;
          overflow: hidden;
          border-radius: 4px;
          position: relative;
          .time {
            position: absolute;
            right: 8px;
            bottom: 0;
            line-height: 27px;
            font-size: 12px;
            color: #fff;
          }
          img {
            width: 154px;
          }
        }
        .text {
          flex: 1;
          margin-left: 12px;
          overflow: hidden;

          h3 {
            font-size: 13px;
            line-height: 29px;
            padding-right: 10px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          .times {
            font-size: 12px;
            line-height: 12px;
            color: #bababa;
          }
          .textBottom {
            margin-top: 24px;
            line-height: 12px;
            height: 12px;
            display: flex;
            justify-content: space-between;
            p {
              width: 170px;
              font-size: 12px;
              line-height: 12px;
              transform: scale(0.8);
              margin-left: -16px;
              color: #848484;
            }
            .iconfont {
              margin-right: 14px;
              color: #787878;
              font-size: 12px;
            }
          }
        }
      }
    }
  }
  .info {
    .basicInfo {
      margin: 0 20px;
      padding-bottom: 50px;
      h3 {
        line-height: 57px;
        font-size: 14px;
        color: #000;
        margin-bottom: 10px;
      }
      .text {
        div {
          height: 19px;
          padding: 10px 0;
          display: flex;
          line-height: 19px;
          font-size: 13px;
          .left {
            width: 82px;
            color: #a5a5a5;
          }
          .right {
            flex: 1;
          }
        }
      }
    }
  }
  .like {
    background-color: #f7f7f7;
    .topMargin {
      width: 100%;
      height: 7px;
    }
    .likeInfo {
      width: 93.6%;
      background-color: #fff;
      margin: 0 auto;
      // margin-top: 7px;
      margin-bottom: 7px;
      border-radius: 5px;
      .top {
        height: 32px;
        padding-top: 17px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .userInfo {
          display: flex;
          justify-content: space-between;
          .img {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            overflow: hidden;
            margin-left: 15px;
            img {
              width: 32px;
              height: 32px;
            }
          }
          p {
            line-height: 32px;
            font-size: 12px;
            padding-left: 6px;
            color: #212121;
          }
        }
        .icon-gengduo1 {
          font-size: 13px;
          color: #8f8f8f;
          padding-right: 11px;
        }
      }
      .text {
        height: 34px;
        line-height: 34px;
        font-size: 14px;
        padding: 0 16px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .img {
        width: 100%;
        overflow: hidden;
        position: relative;
        img {
          width: 100%;
        }
        .imgBottom {
          position: absolute;
          right: 10px;
          bottom: 0;
          height: 28px;
          line-height: 28px;
          font-size: 12px;
          color: rgba(255, 255, 255, 0.9);
        }
      }
      .video {
        width: 100%;
        overflow: hidden;
        position: relative;
        video {
          width: 100%;
          height: 100%;
        }
        .process {
          width: 100%;
          height: 3px;
          position: absolute;
          left: 0;
          bottom: 3px;
          background-color: rgba(255, 255, 255, 0.6);
          .line {
            width: 50%;
            height: 2px;
            background-color: #f0472f;
          }
        }
        .iconfont {
          color: rgba(255, 255, 255, 0.9);
          font-size: 17px;
          font-weight: bold;
          position: absolute;
          left: 13px;
          bottom: 13px;
        }
        .icondisplay {
          display: none;
        }
      }
      .bottom {
        display: flex;
        div {
          flex: 1;
          height: 40px;
          line-height: 40px;
          text-align: center;
          font-size: 13px;
          .iconfont {
            font-size: 15px;
          }
        }
      }
    }
    .likeTips {
      padding-top: 100px;
      font-size: 14px;
      color:#787878;
      text-align: center;
      background-color: #fff;
